





























import Vue from 'vue';
import ValidationRules from '../../utils/InputValidationRules';

export default Vue.extend({
  name: 'MqttServerInfoEditor',
  props: ['value'],
  data: () => ({
    showPassword: false as boolean,
    rules: ValidationRules,
    address: '' as string,
  }),
  methods: {
    onInput() {
      this.$emit('input', this.value);
    },
  },
});
